.notification-detail-container {
  .title-notification {
    font-size: 30px;
    font-weight: bold;
    color: #000000;
    padding: 1.5em 0 1.5em 0;
    line-height: 40px;
  }
  p {
    font-size: 16px;
    line-height: 25px;
    font-weight: 400;
    font-family: 'Google Sans', sans-serif !important;
  }
  div {
    font-size: 16px;
    line-height: 25px;
    font-weight: 400;
    font-family: 'Google Sans', sans-serif !important;
  }
  span {
    font-size: 16px;
    line-height: 18px;
    font-weight: 400;

    font-family: 'Google Sans', sans-serif !important;
  }
  ul {
    list-style: disc;
  }
  li {
    color: #3a556a;
    margin-bottom: 10px;
  }
}
